const mergeImages = function (images, setting) {
    const dpr = window.devicePixelRatio;
    const {
        direction,
        widthSetting,
        alignVertical,
        heightSetting,
        alignHorizontal,
        watermark,
        watermarkColor,
        margin,
        opacity,
        fontSize
    } = setting;
    const canvas = document.getElementById('canvas');
    const ctx = canvas.getContext('2d');
    const largestWidth = Math.max(...images.map(image => image.width));
    const largestHeight = Math.max(...images.map(image => image.height));
    const imagesNew = images.map(image => {
        var widthEqual = image.width;
        var heightWhenWidthEqual = image.height;
        var heightEqual = image.height;
        var widthWhenHeightEqual = image.width;
        if (widthSetting === 'equalWidth') {
            widthEqual = largestWidth;
            heightWhenWidthEqual = image.height / image.width * widthEqual;
        };
        if (heightSetting === 'equalHeight') {
            heightEqual = largestHeight;
            widthWhenHeightEqual = image.width / image.height * heightEqual;
        };
        return {
            url: image.url,
            width: image.width,
            height: image.height,
            widthEqual: widthEqual,
            heightWhenWidthEqual: Math.round(heightWhenWidthEqual),
            heightEqual: heightEqual,
            widthWhenHeightEqual: Math.round(widthWhenHeightEqual),
        }
    });
    console.log('imagesNew:', imagesNew)
    const totalWidth = imagesNew.reduce((a, b) => a + b.widthWhenHeightEqual, 0);
    const totalWidthAddMargin = totalWidth + margin * (imagesNew.length - 1);
    const totalHeight = imagesNew.reduce((a, b) => a + b.heightWhenWidthEqual, 0);
    const totalHeightAddMargin = totalHeight + margin * (imagesNew.length - 1);
    console.log('totalWidth:', totalWidth, 'totalHeight:', totalHeight, 'largestWidth:', largestWidth, 'largestHeight:', largestHeight);
    if (direction === 'vertical') {
        canvas.width = largestWidth;
        canvas.height = totalHeightAddMargin;
        canvas.style.width = `${Math.round(largestWidth / dpr)}px`;
        canvas.style.maxWidth = '80%';
        canvas.style.height = `${canvas.height / canvas.width * canvas.style.width}px`
    } else {
        canvas.width = totalWidthAddMargin;
        canvas.height = largestHeight;
        canvas.style.width = `${Math.round(canvas.width / dpr)}px`;
        canvas.style.maxWidth = '100%';
        canvas.style.height = `${canvas.height / canvas.width * canvas.style.width}px`
    }
    console.log('canvas.style.width:', canvas.style.width, 'canvas.style.height:', canvas.style.height)
    ctx.fillStyle = 'white';
    ctx.fillRect(0, 0, canvas.width, canvas.height);
    const drawImage = function (image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight) {
        const img = new Image();
        img.src = image.url;
        img.onload = async () => {
            console.log(111, img, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight)
            ctx.drawImage(img, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight);
            ctx.save();
            ctx.fillStyle = hexToRgbA(watermarkColor).replace(/,1\)/g, `,${opacity})`);
            ctx.textAlign = 'center';
            ctx.textBaseline = 'middle';
            ctx.font = `${fontSize}px serif`;
            ctx.translate(Math.round(canvas.width / 2), Math.round(canvas.height / 2));
            ctx.rotate(45 * Math.PI / 180);
            const length = Math.sqrt(canvas.width / 2 * canvas.width / 2 + canvas.height / 2 * canvas.height / 2);
            const textWidth = ctx.measureText(watermark).width;
            for (let x = 0; x < length; x = x + textWidth * 1 + fontSize * 2) {
                for (let y = 0; y < length; y = y + fontSize * 3) {
                    if (x === 0 && y === 0) {
                        ctx.fillText(watermark, x, y, length);
                    } else if (x === 0 && y !== 0) {
                        ctx.fillText(watermark, x, y, length);
                        ctx.fillText(watermark, x, -y, length);
                    } else if (x !== 0 && y === 0) {
                        ctx.fillText(watermark, x, y, length);
                        ctx.fillText(watermark, -x, y, length);
                    } else {
                        ctx.fillText(watermark, x, y, length);
                        ctx.fillText(watermark, -x, y, length);
                        ctx.fillText(watermark, x, -y, length);
                        ctx.fillText(watermark, -x, -y, length);
                    }
                }
            }
            ctx.restore();
        }
    };
    for (let [index, image] of imagesNew.entries()) {
        const sx = 0;
        const sy = 0;
        if (direction === 'vertical') {
            if (widthSetting === 'equalWidth') {
                const sWidth = image.width;
                const sHeight = image.height;
                const dx = 0;
                const dy = imagesNew.slice(0, index).reduce((a, b) => a + b.heightWhenWidthEqual, 0) + margin * index;
                const dWidth = image.widthEqual;
                const dHeight = image.heightWhenWidthEqual;
                drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight);
            } else {
                if (alignVertical === 'alignLeft') {
                    const sWidth = image.width;
                    const sHeight = image.height;
                    const dx = 0;
                    const dy = imagesNew.slice(0, index).reduce((a, b) => a + b.height, 0) + margin * index;
                    const dWidth = image.widthEqual;
                    const dHeight = image.heightWhenWidthEqual;
                    drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight);
                } else if (alignVertical === 'alignCenterVertical') {
                    const sWidth = image.width;
                    const sHeight = image.height;
                    const dx = Math.round((largestWidth - image.width) / 2);
                    const dy = imagesNew.slice(0, index).reduce((a, b) => a + b.height, 0) + margin * index;
                    const dWidth = image.widthEqual;
                    const dHeight = image.heightWhenWidthEqual;
                    drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight);
                } else if (alignVertical === 'alignRight') {
                    const sWidth = image.width;
                    const sHeight = image.height;
                    const dx = largestWidth - image.width;
                    const dy = imagesNew.slice(0, index).reduce((a, b) => a + b.height, 0) + margin * index;
                    const dWidth = image.widthEqual;
                    const dHeight = image.heightWhenWidthEqual;
                    drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight);
                }
            }
        } else {
            if (heightSetting === 'equalHeight') {
                const sWidth = image.width;
                const sHeight = image.height;
                const dy = 0;
                const dx = imagesNew.slice(0, index).reduce((a, b) => a + b.widthWhenHeightEqual, 0) + margin * index;
                const dWidth = image.widthWhenHeightEqual;
                const dHeight = image.heightEqual;
                drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight);
            } else {
                if (alignHorizontal === 'alignTop') {
                    const sWidth = image.width;
                    const sHeight = image.height;
                    const dy = 0;
                    const dx = imagesNew.slice(0, index).reduce((a, b) => a + b.width, 0) + margin * index;
                    const dWidth = image.widthWhenHeightEqual;
                    const dHeight = image.heightEqual;
                    drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight);
                } else if (alignHorizontal === 'alignCenterHorizontal') {
                    const sWidth = image.width;
                    const sHeight = image.height;
                    const dy = Math.round((largestHeight - image.height) / 2);
                    const dx = imagesNew.slice(0, index).reduce((a, b) => a + b.width, 0) + margin * index;
                    const dWidth = image.width;
                    const dHeight = image.height;
                    drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight);
                } else if (alignHorizontal === 'alignBottom') {
                    const sWidth = image.width;
                    const sHeight = image.height;
                    const dy = largestHeight - image.height;
                    const dx = imagesNew.slice(0, index).reduce((a, b) => a + b.width, 0) + margin * index;
                    const dWidth = image.widthWhenHeightEqual;
                    const dHeight = image.heightEqual;
                    drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight);
                }
            }
        };
    };
};

const getThumbnail = async function (images) {
    function getOneThumbnail(image) {
        return new Promise((resolve, reject) => {
            const canvas = document.createElement('canvas');
            const ctx = canvas.getContext('2d');
            canvas.width = 100;
            canvas.height = 100;
            const sWidth = Math.min(image.height, image.width);
            const sHeight = Math.min(image.height, image.width);
            const img = new Image();
            img.src = image.url;
            img.onload = () => {
                ctx.fillStyle = 'white';
                ctx.fillRect(0, 0, 100, 100);
                ctx.drawImage(img, 0, 0, sWidth, sHeight, 0, 0, 100, 100);
                const thumbnail = canvas.toDataURL('image/jpeg');
                resolve(thumbnail);
                reject('get thumbnail 出错')
            }
        })
    };
    const promiseArray = images.map(image => getOneThumbnail(image));
    return await Promise.all(promiseArray);
}

const hexToRgbA = function (hex) {
    var c;
    if (/^#([A-Fa-f0-9]{3}){1,2}$/.test(hex)) {
        c = hex.substring(1).split('');
        if (c.length === 3) {
            c = [c[0], c[0], c[1], c[1], c[2], c[2]];
        }
        c = '0x' + c.join('');
        return 'rgba(' + [(c >> 16) & 255, (c >> 8) & 255, c & 255].join(',') + ',1)';
    }
    throw new Error('Bad Hex');
}


export default () => {
    const [images, setImages] = useState([]);
    const [thumbnails, setThumbnails] = useState([]);
    const [setting, setSetting] = useState({
        direction: 'vertical',
        widthSetting: 'equalWidth',
        alignVertical: 'alignCenterVertical',
        heightSetting: 'equalHeight',
        alignHorizontal: 'alignCenterHorizontal',
        watermark: '',
        watermarkColor: '#e66465',
        margin: 10,
        opacity: 0.3,
        fontSize: 50,
    });
    const [margin, setMargin] = useState(setting.margin);
    const [opacity, setOpacity] = useState(setting.opacity);
    const [fontSize, setFontSize] = useState(setting.fontSize);

    var elementBeingDragged = '';
    useEffect(() => {
        (async () => {
            setThumbnails(await getThumbnail(images))
        })()
    }, [images]);
    useEffect(() => {
        mergeImages(images, setting);
    }, [images, setting]);

    const onSelectImages = async (e) => {
        const files = [...e.target.files].map(image => URL.createObjectURL(image));
        console.log('files:', files)
        const getImageSize = (imageUrl) => {
            return new Promise((resolve, reject) => {
                const img = new Image();
                img.src = imageUrl;
                img.onload = () => {
                    resolve({
                        width: +img.width,
                        height: +img.height,
                        url: img.src
                    });
                    reject('出错')
                }
            })
        };
        const imagesWithSizeInfo = await Promise.all(files.map(file => getImageSize(file)));
        setImages(imagesWithSizeInfo)
    };

    const deleteImage = function (e) {
        const index = e.target.dataset.index;
        const imagesTemp = JSON.parse(JSON.stringify(images))
        imagesTemp.splice(index, 1)
        setImages(imagesTemp)
    }

    const saveImage = function () {
        const canvas = document.getElementById('canvas');
        const a = document.getElementById('download');
        if (canvas.width === 0) {
            a.removeAttribute('href')
        } else {
            const outputImage = canvas.toDataURL('image/jpeg');
            a.href = outputImage;
        }
    };

    const onDrag = function (e) {
        elementBeingDragged = e.target;
    }

    const onDrop = function (e) {
        const elementBeingSwap = e.target;
        elementBeingSwap.style.border = 'none';
        if (elementBeingSwap.src === elementBeingDragged.src) return;
        const imagesTemp = JSON.parse(JSON.stringify(images));
        const index_1 = imagesTemp.map(image => image.url).indexOf(elementBeingSwap.src)
        const index_2 = imagesTemp.map(image => image.url).indexOf(elementBeingDragged.src)
        const tempElement = imagesTemp[index_1];
        imagesTemp[index_1] = imagesTemp[index_2];
        imagesTemp[index_2] = tempElement;
        setImages(imagesTemp);
    }

    const onDragOver = function (e) {
        e.preventDefault();
    }

    const onDragEnter = function (e) {
        e.target.style.border = '1px solid red'
    }

    const onDragLeave = function (e) {
        e.target.style.border = 'none';
    }

    const changeForm = function (e) {
        const settingName = e.target.name;
        const settingValue = e.target.value;
        if (!['margin', 'fontSize', 'opacity'].includes(settingName)) {
            const settingTemp = JSON.parse(JSON.stringify(setting));
            settingTemp[settingName] = settingValue;
            setSetting(settingTemp)
        }
    }

    const debounceChangeSlider = useCallback(debounce((value, name) => {
        const settingTemp = JSON.parse(JSON.stringify(setting));
        settingTemp[name] = value;
        setSetting(settingTemp)
    }, 250), [setting]);

    const onChangeSlider = function (e) {
        const value = e.target.value;
        const name = e.target.name;
        switch (name) {
            case 'margin': {
                setMargin(value);
                break;
            }
            case 'fontSize': {
                setFontSize(value);
                break;
            }
            case 'opacity': {
                setOpacity(value);
                break;
            }
            default: {
                break;
            }
        };
        setMargin(margin);
        debounceChangeSlider(value, name)
    }

    return (
        <Layout>
            <Helmet>
                <title>图片拼接 | 长图拼接</title>
                <meta
                    name="description"
                    content="图片拼接, 把多张图片合并成一张, 支持纵向或横向拼接, 可以把多张图片拼接成一张长图片, 还能添加水印."
                />
            </Helmet>
            <h1 css={hStyle}>图片拼接 | 长图拼接</h1>
            <section>
                <form onChange={changeForm} css={formStyle} >
                    <div className="form-radio" >
                        <p>拼接方向</p>
                        <label for="vertical">
                            <input type="radio" id="vertical"
                                name="direction" value="vertical" checked={setting.direction === 'vertical'} />
                            纵向拼接
                            </label>
                        <label for="horizontal">
                            <input type="radio" id="horizontal"
                                name="direction" value="horizontal" checked={setting.direction === 'horizontal'} />
                                横向拼接
                                </label>
                    </div>
                    {
                        setting.direction === 'vertical' ? (
                            <div className="form-radio" >
                                <p>宽度设置</p>

                                <label for="equalWidth">
                                    <input type="radio" id="equalWidth"
                                        name="widthSetting" value="equalWidth" checked={setting.widthSetting === 'equalWidth'} />
                                    等宽
                                    </label>
                                <label for="originalWidth">
                                    <input type="radio" id="originalWidth"
                                        name="widthSetting" value="originalWidth" checked={setting.widthSetting === 'originalWidth'} />
                                    原始宽度
                                    </label>
                            </div>
                        ) : (
                                <div className="form-radio" >
                                    <p>高度设置</p>
                                    <label for="equalHeight">
                                        <input type="radio" id="equalHeight"
                                            name="heightSetting" value="equalHeight" checked={setting.heightSetting === 'equalHeight'} />
                                        等高
                                        </label>
                                    <label for="originalHeight">
                                        <input type="radio" id="originalHeight"
                                            name="heightSetting" value="originalHeight" checked={setting.heightSetting === 'originalHeight'} />
                                        原始高度
                                        </label>
                                </div>
                            )
                    }
                    {
                        setting.direction === 'vertical' ? (
                            <div className="form-radio" >
                                <p>对齐方式</p>
                                <label for="alignLeft">
                                    <input type="radio" id="alignLeft"
                                        name="alignVertical" value="alignLeft" checked={setting.alignVertical === 'alignLeft'} disabled={setting.widthSetting === 'equalWidth'} />
                                    左对齐
                                    </label>
                                <label for="alignCenterVertical">
                                    <input type="radio" id="alignCenterVertical"
                                        name="alignVertical" value="alignCenterVertical" checked={setting.alignVertical === 'alignCenterVertical'} disabled={setting.widthSetting === 'equalWidth'} />
                                    左右居中
                                    </label>
                                <label for="alignRight">
                                    <input type="radio" id="alignRight"
                                        name="alignVertical" value="alignRight" checked={setting.alignVertical === 'alignRight'} disabled={setting.widthSetting === 'equalWidth'} />
                                    右对齐
                                    </label>
                            </div>
                        ) : setting.direction === 'horizontal' ? (
                            <div className="form-radio" >
                                <p>对齐方式</p>
                                <label for="alignTop">
                                    <input type="radio" id="alignTop"
                                        name="alignHorizontal" value="alignTop" checked={setting.alignHorizontal === 'alignTop'} disabled={setting.heightSetting === 'equalHeight'} />
                                    上对齐
                                    </label>
                                <label for="alignCenterHorizontal">
                                    <input type="radio" id="alignCenterHorizontal"
                                        name="alignHorizontal" value="alignCenterHorizontal" checked={setting.alignHorizontal === 'alignCenterHorizontal'} disabled={setting.heightSetting === 'equalHeight'} />
                                    上下居中
                                    </label>
                                <label for="alignBottom">
                                    <input type="radio" id="alignBottom"
                                        name="alignHorizontal" value="alignBottom" checked={setting.alignHorizontal === 'alignBottom'} disabled={setting.heightSetting === 'equalHeight'} />
                                    下对齐
                                    </label>
                            </div>
                        ) : ''
                    }
                    <div className="form-text-input" >
                        <p>自定义水印</p>
                        <input type="text" id="watermark" name="watermark" placeholder="请输入要显示的水印..." value={setting.water} />
                    </div>
                    <div className="form-color" >
                        <p>水印颜色</p>

                        <label for="watermarkColor">
                            <input type="color" id="watermarkColor" name="watermarkColor" value={setting.watermarkColor} />
                            {setting.watermarkColor}
                        </label>
                    </div>
                    <div className="form-slider" >
                        <p>水印透明度</p>
                        <label for="opacity">
                            <input type="range" id="opacity" name="opacity" min="0" max="1" step="0.1" value={opacity} onChange={onChangeSlider} />
                            {opacity}
                        </label>
                    </div>
                    <div className="form-slider" >
                        <p>水印大小</p>
                        <label for="fontSize">
                            <input type="range" id="fontSize" name="fontSize" min="1" max="500" value={fontSize} onChange={onChangeSlider} />
                            {fontSize}px
                            </label>
                    </div>
                    <div className="form-slider" >
                        <p>图片间距</p>
                        <label for="margin">
                            <input type="range" id="margin" name="margin" min="0" max="500" value={margin} onChange={onChangeSlider} />
                            {margin}px
                            </label>
                    </div>
                </form>
            </section>
            <section>
                <div css={buttonsStyle}>
                    <div css={chooseImageBtnStyle}>
                        <label htmlFor="input-file" css={imageBtnStyle}>选择图片</label>
                        <input id="input-file" type="file" onChange={onSelectImages} multiple accept="image/*"></input>
                    </div>
                    <a download onClick={saveImage} id="download" href="https://kejiweixun.com/merge-images/" css={imageBtnStyle} role="button">保存图片</a>
                </div>
                <div css={thumbnailStyle}>
                    {
                        thumbnails.map((thumbnail, index) => {
                            return (
                                <div key={thumbnail} draggable="true" onDragStart={onDrag} onDrop={onDrop} onDragOver={onDragOver} onDragEnter={onDragEnter} onDragLeave={onDragLeave}>
                                    <img src={thumbnail} className="image-thumbnail" alt="thumbnail"></img>
                                    <img src={deleteIcon} className="delete-icon" onClick={deleteImage} data-index={index} alt="delete icon"></img>
                                </div>
                            )
                        })
                    }
                </div>
                <canvas id="canvas" css={canvasStyle} width="0px" height="0px" ></canvas>
            </section>
            <ISSO title="图片拼接" slug={`merge-images/`} />
        </Layout>
    )
}